body {
  font-size: 15px;
  margin: 0;
  padding: 0;
  background: #1488CC;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #2B32B2, #1488CC); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  font-family: Consolas, monaco, monospace;
}

p {
  color: #d9ecf5;
}
nav p {
    position: relative;
    top: -1rem;
}

.dim:hover,
.dim:focus {
  opacity: .8 !important;
}

.user-icon {
  width: 25px;
  height: auto;
  vertical-align: middle;
  padding: .78rem;
}
.Tilt {
    background: #1488CC;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #2B32B2, #1488CC); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.logo {
    width: 60px;
    height: 60px;
}
table {
    ont-size: 16px;
}

th, td {
    vertical-align: middle;
}
.form {
    max-width: 700px;
    background:
    linear-gradient(27deg, #151515 5px, transparent 5px) 0 5px,
    linear-gradient(207deg, #151515 5px, transparent 5px) 10px 0px,
    linear-gradient(27deg, #222 5px, transparent 5px) 0px 10px,
    linear-gradient(207deg, #222 5px, transparent 5px) 10px 5px,
    linear-gradient(90deg, #1b1b1b 10px, transparent 10px),
    linear-gradient(#1d1d1d 25%, #1a1a1a 25%, #1a1a1a 50%, transparent 50%, transparent 75%, #242424 75%, #242424);
    background-color: #131313;
    background-size: 20px 20px;
}

button {
    background-color: #2B32B2;
    cursor: pointer;
    border-width: 4px;
    border-color: #ffffff99;
}

input::-webkit-input-placeholder {
    font-family: Consolas, monaco, monospace;
}

input:-ms-input-placeholder {
    font-family: Consolas, monaco, monospace;
}

input::-ms-input-placeholder {
    font-family: Consolas, monaco, monospace;
}

input::placeholder {
    font-family: Consolas, monaco, monospace;
}

.user-camera {
    padding: 0 .57rem;
}

.user-camera svg {
    width: 2.2rem;
    height: 1.9rem;
}
.bounding-box {
    position: absolute;
    box-shadow: 0 0 0 3px #149df2 inset;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.face-img {
  max-width: 500px;
  height: auto;
}
.app {
  text-align: center;
}

.center-column {
  flex-direction: column;
  display: flex;
  align-items: center;
}

.center-row {
  flex-direction: row;
  display: flex;
  align-items: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
}

/* loading icon */
#loader {
  visibility: hidden;
  display: inline-block;
  position: absolute;
  top: 12px;
  width: 30px;
  height: 30px;
}

#loader div {
  position: absolute;
  top: 0;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #001B43;
  -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
          animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
#loader div:nth-child(1) {
  left: 6px;
  -webkit-animation: loader1 0.6s infinite;
          animation: loader1 0.6s infinite;
}
#loader div:nth-child(2) {
  left: 6px;
  -webkit-animation: loader2 0.6s infinite;
          animation: loader2 0.6s infinite;
}
#loader div:nth-child(3) {
  left: 26px;
  -webkit-animation: loader2 0.6s infinite;
          animation: loader2 0.6s infinite;
}
#loader div:nth-child(4) {
  left: 45px;
  -webkit-animation: loader3 0.6s infinite;
          animation: loader3 0.6s infinite;
}
@-webkit-keyframes loader1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes loader1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@-webkit-keyframes loader3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
@keyframes loader3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
}
@-webkit-keyframes loader2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0);
  }
}
@keyframes loader2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
  }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0);
  }
}
